.footer {
  display: flex;
  margin-top: auto;
  padding: 20px 0px;
  color: white;

  background: var(--footer-gradient);
}

.contentWrapper {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
  }
}

.linksBox {
  display: flex;
  justify-content: center;

  margin-top: 20px;
  @media screen and (min-width: 768px) {
    margin-top: 0px;
  }
}

.link {
  &:not(:first-of-type) {
    margin-left: 10px;
  }

  &:hover .icon,
  &:focus .icon {
    fill: white;
  }
}

.iconsLogoBox {
  @media screen and (min-width: 768px) {
    display: flex;

    justify-content: space-between;
    align-items: center;

    width: 100%;
    padding-right: 40px;
  }
}

.icon {
  width: 36px;
  height: auto;

  fill: var(--footer-text-color);

  transition: fill var(--transition-duration) ease-in-out;
}

.contactsLinksBox {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 1200px) {
    flex-direction: row;
    align-items: center;
    width: 100%;
  }
}

.contactsLink {
  display: flex;
  align-items: center;

  &:not(:first-of-type) {
    margin-top: 10px;

    @media screen and (min-width: 1200px) {
      margin-top: 0px;
      margin-left: 20px;
    }
  }

  &:hover .contactsIcon,
  &:focus .contactsIcon {
    stroke: white;
  }

  &:hover .contactsLinkText,
  &:focus .contactsLinkText {
    color: white;
  }

  text-decoration: none;
}

.contactsLinkText {
  margin-left: 10px;
  font-family: var(--secondary-font-family);
  font-size: 20px;

  color: var(--footer-text-color);

  transition: color var(--transition-duration) ease-in-out;
}

.contactsIcon {
  width: 40px;
  height: auto;

  stroke: var(--footer-text-color);
  transition: stroke var(--transition-duration) ease-in-out;
}
